		aboutUs:'About us',
		aboutUsContent:'We sell, package and guide you in the area of Stockholm Country. Active outdoor holidays in Sweden, our work is to get you out in the wild. Our guests our mostly people from abroad that fly to Sweden. We do work from both Arlanda and Skavsta.Our base is Nyk√∂ping/Trosa and for you visiting us we help arrange transportation to where your adventure starts. If you rathet go by car och motorcycle or train, that¬¥s no problem at all. Most of our biking and hiking packages our selfguided but our kayak tours are always with an experienced guide.',
		enviromentStatement:'Enviroment-statement',
		enviromentTextOne:'The fact that owning a travel company based on air travel by 2018 is one of the dumbest things we can do in view of the negative impact the aircraft has on our planet. I know! That people fly is a prerequisite for our business. Therefore, we have produced the following environmental statement for our business. It does not excuse the environmental impact our customers have on their air travel, but it is our commitment to the environment, since we believe that the desire to discover new things is not limited by whether we run this company or not.',
		enviromentTextTwo:'We climate compensate for all our guests flights. We arrange our trips based on a long-term and sustainable intent to minimize damage and adverse environmental impacts. For example, a brief environmental introduction is included before you start the tour.',
		enviromentTextThree:'We try as long as it is possible to choose transportioin and fuel with the least emissions per passenger. This means that we would rather buy a ticket for the train for you than to send a taxi to pick you up in Stockholm.',
