		walkingText:'Going for a trip will be a wonderful experience and we want to be sure that our equipment is of the highest grade. All our tents, sleeping bags and backpacks are from the Norwegian Bergans. Mats is from world-leading Exped. Cooking equipment such as burners, etc. are from Swedish Primus.',
