		responsibleTextThree:'We are not responsible for promises that the supplier or his or her contact may have made directly to you without our knowledge and which we were not aware of for services and products in addition to that set out in the description which were purchased directly from the supplier. (If you have entered into an agreement on something that is not covered by the organiser‚Äôs agreement, make sure that you are given it in writing and signed by an owner or contact).',
		travelGuarantee:'TRAVEL GUARANTEE',
