		luggage:'LUGGAGE TRANSPORT',
		luggageText:'Luggage transport applies only to adults. For cycle tours where luggage transport is not included and bought seperately all participants need to be added, 1 bag per person, max 20 kg per bag. It is not permitted to leave valuables such as money, jewellery, cameras, etc. with the baggage transport service. You must always keep your valuables with you and take responsibility for their safe storage yourself.',
		wrong:'IF SOMETHING GOES WRONG',
		wrongTextOne:'If all or parts of your journey arrangements cannot be delivered as in the confirmation, and if no replacement arrangements can be made, whose difference from those booked is so small as to be insignificant to you (e.g. a hotel of the same class and location), you have the right to cancel your booking. In such cases SkavstaAdventure will repay what you paid them, with a deduction for the parts of the arrangement that you may have already used. If a transport service or an event included in your booking is cancelled at short notice, a refund will only be given for the transport service/event concerned. If you have complaints you must make them to us within 3 days of the arrival date. Errors arising during your stay must be reported immediately. This should initially be done to the seller/facility. If you have not sought redress during your stay and thus not given the seller/facility an opportunity to correct any defects, you cannot demand compensation afterwards.',
