			textFour:'If the services promised in the package do not match or fail to meet the requirements made, contact us as soon as possible so that we have a chance to correct the problems, see the text above on If something goes wrong.',
			textFive:'SkavstaAdventure shall be responsible for making pre-travel preparations, careful selection and monitoring of the service providers as well as for the accuracy of the descriptions of service valid for the period of travel at the time our website is updated.',
			textSix:'Travellers shall take part in our cycling journeys at their own risk. Minors may only participate in a journey when accompanied by a parent or guardian.',
			textSeven:'Every travel participant shall be responsible for ensuring that his or her health is suitable for the requirements of travel.',
			textEight:'In addition, every travel participant shall be responsible for the observance of traffic regulations.',
			textNine:'The conditions of carriage of the particular transport companies shall apply to all means of transport (bus, boat, airplane, etc.).',
			textTen:'If, due to reasons beyond our control (adaptations, renovations, etc.) a scheduled tour cannot take place, we cannot assume any liability.',
			textEleven:'In the event of damage caused solely by one of the service providers selected by us or which is not due to premeditation or gross negligence on our part, then our liability shall be limited, regardless of the legal basis, to three times the price of the journey.',
			textTwelve:'In the event of loss or damage to travel luggage, we shall only be liable if the loss or damages is caused by us and is communicated to us immediately after the occurrence. In this event, we shall only be liable for up to a max. of SEK 2000 per person. We assume no liability for: items which are not usually transported in travel luggage, means of payment of any sort, for visual damage and damage to handles and wheels or damages to pieces of luggage weighing more than a total of 20 kg. We recommend that you take out a travel security package to cover these risks.',
			textThirteen:'If a journey is cancelled due to reasons beyond our control (strike, natural disaster, etc.) or if the minimum number of participants is not reached, we reserve the right to cancel the journey up to 30 days before the planned travel date. In this event you shall be notified without delay and will be refunded any amounts already paid to us. You shall not be entitled to any compensation beyond this.',
