	disputes:'IN THE EVENT OF DISPUTES',
	disputesText:'Contact us first of all with any complaints, If we fail to agree, contact the National Board for Consumer Disputes. It consists of an impartial chair and representatives of travel organisers and consumers. Consumer advisors in your home municipality can also help you. If possible, complaints should be made during your stay. If, as a customer, you make a complaint that is not unjustified, SkavstaAdventure will immediately take action to find an appropriate solution.',
	forceMejaure:'FORCE MAJEURE',
